.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.kanban-table {
  margin-top: 10px;
  overflow-x: auto;
}

.kanban-row {
  display: flex;
  gap: 20px;
  align-items: flex-start;
}

.kanban-column {
  flex: 1;
  min-width: 180px;     /* tighter columns */
  border: 1px solid #ccc;
  padding: 5px;         /* reduced padding */
  border-radius: 6px;
  background: #f9f9f9;
}

.kanban-column h3 {
  text-align: center;
  margin-bottom: 5px;
  font-size: 14px;      /* smaller text */
  background: #eee;
  padding: 3px;
  border-radius: 4px;
}

.task-card {
  background: #fff;
  border: 1px solid #ddd;
  padding: 5px;         /* compact padding */
  margin-bottom: 5px;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.2s;
  font-size: 13px;      /* smaller font */
}

.task-card:hover {
  background: #e6f2ff;
}

/* Compact class ensures everything is tight */
.compact {
  gap: 10px !important;
  padding: 4px !important;
  margin: 0 !important;
}


.popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-content {
  background: white;
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  text-align: center;
}

.popup-content button {
  margin-top: 15px;
  padding: 8px 12px;
  border: none;
  background: #007bff;
  color: white;
  border-radius: 6px;
  cursor: pointer;
}

.popup-content button:hover {
  background: #0056b3;
}

